}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Poppins Medium"), url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Poppins Bold"), url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff");
}
.modal {
  z-index: 1;
  display: none;
}
.modal--nojs {
  display: block;
}

.modal__close {
  position: absolute;
  top: 18px;
  right: 20px;
  width: 14px;
  height: 14px;
  display: block;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
}
@media (max-width: 1023px) {
  .modal__close {
    width: 20px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .modal__close {
    top: 12px;
    right: 15px;
    width: 14px;
    height: 14px;
  }
}

.modal-show {
  position: fixed;
  left: 50%;
  z-index: 2;
  transform: translateX(-50%);
  display: block;
}

.wrapper {
  max-width: 1170px;
  min-width: 1024px;
  margin: 0 auto;
}
@media (min-width: 1024px) and (max-width: 1365px) {
  .wrapper {
    padding: 0 20px;
  }
}
@media (max-width: 1023px) {
  .wrapper {
    max-width: 1023px;
    min-width: 767px;
  }
}
@media (max-width: 767px) {
  .wrapper {
    max-width: 767px;
    min-width: 320px;
  }
}

.page-header {
  position: relative;
}
.page-header a {
  color: #1d1613;
}
.page-header a:hover, .page-header a:focus {
  color: #a87b62;
  outline-color: #a87b62;
}
.page-header a:active {
  color: #a87b62;
  opacity: 0.6;
}
.page-header--catalog::after, .page-header--card::after {
  top: 125px;
}
.page-header--catalog .page-header__wrapper, .page-header--card .page-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 40px calc(50% - 605px) 49px;
  border-bottom: 1px solid #f6f5f4;
  margin: 0 20px;
}
.page-header--catalog .page-header__cart, .page-header--card .page-header__cart {
  top: 53px;
}
.page-header--catalog .page-header__nav {
  display: none;
}
.page-header--card::after {
  box-shadow: 0 61px 0 0 #f6f5f4;
}

.page-header__wrapper {
  position: relative;
  padding-top: 42px;
  padding-bottom: 18px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.page-header__toggle {
  display: none;
}

.page-header__logo {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.page-header__logo svg {
  fill: #a87b62;
}

.page-header__login {
  font-size: 14px;
  line-height: 21px;
  margin-right: 80px;
}

.page-header__cart {
  font-size: 14px;
  line-height: 21px;
  position: absolute;
  top: 42px;
  right: 5px;
}
.page-header__cart svg {
  display: none;
}

.page-header__nav {
  width: 100%;
  margin-top: 69px;
}

.page-header__site-menu {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 16px;
  line-height: 24px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.page-header__site-menu li:not(:last-child) {
  margin-right: 36px;
}
.page-header__site-menu--about {
  display: none;
}

@media (max-width: 1023px) {
  .page-header {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    min-height: 126px;
    min-width: 100%;
  }
  .page-header::after {
    display: none;
  }
  .page-header a {
    color: #ffffff;
  }
  .page-header a:hover, .page-header a:focus {
    color: #1d1613;
  }
  .page-header a:active {
    color: #1d1613;
    opacity: 0.6;
  }
  .page-header--catalog .page-header__nav, .page-header--card .page-header__nav {
    display: block;
  }

  .page-header__wrapper {
    padding: 136px 44px 0 46px;
    flex-direction: column;
  }

  .page-header__cart {
    top: 55px;
    right: 45px;
  }
  .page-header__cart span {
    display: none;
  }
  .page-header__cart svg {
    display: block;
  }

  .page-header__nav {
    margin-top: 46px;
  }

  .page-header__site-menu {
    font-size: 24px;
    line-height: 36px;
    flex-direction: column;
  }
  .page-header__site-menu li:not(:last-child) {
    margin-right: 0;
    margin-bottom: 21px;
  }
  .page-header__site-menu li:last-child {
    margin-bottom: 40px;
  }
  .page-header__site-menu--about {
    font-size: 18px;
    line-height: 23px;
    display: block;
  }
  .page-header__site-menu--about a {
    color: rgba(255, 255, 255, 0.75);
  }

  .page-header__login {
    font-size: 18px;
    line-height: 23px;
    order: 1;
  }

  .page-header__toggle {
    position: absolute;
    top: 52px;
    left: 45px;
    z-index: 2;
    display: block;
    width: 25px;
    height: 19px;
    background-color: transparent;
    cursor: pointer;
    border: none;
  }
  .page-header__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 25px;
    height: 3px;
  }

  .page-header--opened .page-header__toggle::before {
    background-color: #ffffff;
    box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
  }
  .page-header--opened .page-header__logo svg {
    fill: #ffffff;
  }
  .page-header--opened .page-header__cart svg {
    stroke: #ffffff;
  }

  .page-header--opened.page-header {
    min-height: 778px;
    background-color: #a87b62;
  }

  .page-header--opened.page-header--catalog .page-header__wrapper {
    padding-top: 136px;
  }

  .page-header--opened.page-header--card .page-header__wrapper {
    padding-top: 136px;
  }

  .page-header--closed .page-header__wrapper {
    padding-top: 0;
  }
  .page-header--closed .page-header__toggle::before {
    background-color: #1d1613;
    box-shadow: 0 8px 0 0 #1d1613, 0 16px 0 0 #1d1613;
  }
  .page-header--closed .page-header__logo svg {
    fill: #a87b62;
  }
  .page-header--closed .page-header__cart svg {
    stroke: #1d1613;
  }
  .page-header--closed .page-header__site-menu,
.page-header--closed .page-header__login,
.page-header--closed .page-header__form {
    display: none;
  }

  .page-header--closed.page-header {
    background-color: #ffffff;
  }

  .page-header--nojs {
    position: static;
  }
}
@media (max-width: 767px) {
  .page-header {
    min-height: 92px;
  }
  .page-header--card .page-header__wrapper {
    padding-bottom: 55px;
  }
  .page-header--catalog .page-header__cart, .page-header--card .page-header__cart {
    top: 38px;
  }

  .page-header--opened.page-header {
    min-height: 684px;
    background-color: #a87b62;
  }

  .page-header__wrapper {
    padding: 102px 15px 0 16px;
  }

  .page-header__logo {
    top: 20px;
  }
  .page-header__logo svg {
    width: 52px;
    height: 52px;
  }

  .page-header__cart {
    top: 38px;
    right: 15px;
  }

  .page-header__nav {
    margin-top: 37px;
  }

  .page-header__site-menu li:last-child {
    margin-bottom: 30px;
  }

  .page-header__toggle {
    top: 37px;
    left: 16px;
  }
}
