  text-align: center;
}

.slider__link {
  position: relative;
  color: #1d1613;
}
.slider__link::after {
  position: absolute;
  top: 21px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #a87b62;
  content: "";
}
.slider__link:hover, .slider__link:focus {
  color: #a87b62;
  outline-color: #a87b62;
}
.slider__link:hover::after, .slider__link:focus::after {
