}
.slider__link:active {
  color: #a87b62;
  opacity: 0.6;
}
.slider__link:active::after {
  background-color: #1d1613;
}

.slider__buttons {
  position: relative;
  top: -48px;
  width: 100%;
}

.slider__button {
  position: absolute;
  top: 0;
  width: 30px;
  height: 10px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.slider__button--left {
  left: -22px;
  top: -4px;
}
.slider__button--right {
  right: 5px;
}
.slider__button svg {
  stroke: #a87b62;
}
.slider__button:hover, .slider__button:focus {
  outline-color: #a87b62;
}
.slider__button:hover svg, .slider__button:focus svg {
  stroke: #1d1613;
}
